.recipe {
/*  background: url('../img/chalkboard.jpg');
  background-size: cover;*/
  color: #black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  position: relative;
  border-radius: 15px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
}

.recipe:hover {
/*  --translate-y: -2px;*/
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .5);
}

.recipe.flip {
   --rotate-y: 180deg;
}

.recipe__image {
  border-radius: 10px 10px 30% 0;
  height:300px;
  object-fit: cover;
  width: 100%;
}

.recipe .front,
.recipe .back {
  position: absolute;
  backface-visibility: hidden;
  background: white;
  border-radius: 15px;
  transform: rotateX(0deg)
}

.recipe .back {
  width: 100%;
  transform: rotateY(180deg);
  color: black;
  padding: 20px;
  left: 0;
}

.recipe__title {
  padding: 40px 20px;
  font-size: 1.3rem;
  color: #bf358f;
  font-family: 'Fredericka the Great', cursive;
  text-transform: uppercase;
  letter-spacing:4px;
  text-align: center;
}

.recipe__header {
  display:flex;
  justify-content: center;
  align-items: center;
}

.recipe__row {
  margin-top: 10px;
}

.recipe__label {
  margin-right: 5px;
  text-transform: uppercase;
  font-size:14px;
  font-weight: 700;
  background: linear-gradient(180deg, rgba(255,255,255,0) 65%, pink 65%);
  border-radius:0 0 5px 1px;
}

.recipe__author{
  padding-top: 20px;
  text-align: right;
  font-family: 'Indie Flower', cursive;
  font-size:18px;
}

.recipe__author .recipe__label {
  text-transform: none;
  font-weight: normal;
  background:none;
  font-size:18px;
}

.recipe__value--indented {
  margin-top:10px;
  margin-left: 6px;
}

.recipe__instructions {
  white-space: pre-line;
}
