.recipe-list__add-recipe-btn-container {
  padding: 30px 0;
  text-align: center;
}

.my-masonry-grid {
  padding: 50px 0; /* Not needed if autoprefixing */ /* Not needed if autoprefixing */
  display: flex;
  margin-left: -30px; /* gutter size offset */
  width: auto;
}

.my-masonry-grid_column {
  padding-left: 30px; /* gutter size */
  background-clip: padding-box;
}

/* Style your items */
.my-masonry-grid_column > div { /* change div to reference your elements you put in <Masonry> */
  margin-bottom: 30px;
}

.btn-container {
  display: flex;
  justify-content: flex-end;
}
.btn {
  cursor: pointer;
  margin-left:14px;
  font-weight: bold;
}
.btn--danger {
  font-size: 22px;
  background: none;
  border: none;
  outline: none;
  border-radius: 5px;
/*  color:hsl(301, 76%, 87%);*/
}

.btn:hover,
.btn:focus {
  cursor: pointer;
  margin-left:14px;
  color: hsl(301, 76%, 77%);
}

.recipe {
/*  background: url('../img/chalkboard.jpg');
  background-size: cover;*/
  color: #black;
  box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
  position: relative;
  border-radius: 15px;
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .3);
  transform-style: preserve-3d;
  transition: 150ms;
  cursor: pointer;
  transform: perspective(1000px) rotateY(0) translateY(0);
  transform: perspective(1000px) rotateY(var(--rotate-y, 0)) translateY(var(--translate-y, 0));
}

.recipe:hover {
/*  --translate-y: -2px;*/
  box-shadow: 0 0 5px 2px rgba(0, 0, 0, .5);
}

.recipe.flip {
   --rotate-y: 180deg;
}

.recipe__image {
  border-radius: 10px 10px 30% 0;
  height:300px;
  object-fit: cover;
  width: 100%;
}

.recipe .front,
.recipe .back {
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
  background: white;
  border-radius: 15px;
  transform: rotateX(0deg)
}

.recipe .back {
  width: 100%;
  transform: rotateY(180deg);
  color: black;
  padding: 20px;
  left: 0;
}

.recipe__title {
  padding: 40px 20px;
  font-size: 1.3rem;
  color: #bf358f;
  font-family: 'Fredericka the Great', cursive;
  text-transform: uppercase;
  letter-spacing:4px;
  text-align: center;
}

.recipe__header {
  display:flex;
  justify-content: center;
  align-items: center;
}

.recipe__row {
  margin-top: 10px;
}

.recipe__label {
  margin-right: 5px;
  text-transform: uppercase;
  font-size:14px;
  font-weight: 700;
  background: linear-gradient(180deg, rgba(255,255,255,0) 65%, pink 65%);
  border-radius:0 0 5px 1px;
}

.recipe__author{
  padding-top: 20px;
  text-align: right;
  font-family: 'Indie Flower', cursive;
  font-size:18px;
}

.recipe__author .recipe__label {
  text-transform: none;
  font-weight: normal;
  background:none;
  font-size:18px;
}

.recipe__value--indented {
  margin-top:10px;
  margin-left: 6px;
}

.recipe__instructions {
  white-space: pre-line;
}

.ingredient-grid {
  display: grid;
  grid-template-columns: repeat(2, auto);
  grid-row-gap: 5px;
  row-gap: 5px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
  justify-content: flex-start;
}


.recipe-edit {
  font-size: 14px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  padding: 20px;
  border-radius: 10px;
  z-index: 1000;
  max-height: 100%;
  overflow-y: auto;
}

.overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 1000;
}

.recipe-edit__button-container {
  margin-top: 20px;
  text-align: center;
}

.recipe-edit__details-grid {
  display: grid;
  grid-row-gap: 10px;
  row-gap: 10px;
  grid-column-gap: 40px;
  -webkit-column-gap: 40px;
          column-gap: 40px;
}

.recipe-edit__label {
  font-weight: bold;
}

.recipe-edit__input {
  border: 1px solid black;
  border-radius: 5px;
  font-size: inherit;
  padding: 5px 10px;
  outline: none;
  width: 100%;
}

.recipe-edit__details-grid:last-child {
  padding: 20px 0;
}

textarea.recipe-edit__input {
  resize: none;
  height: 100px;
  margin: 8px 0;
}

.recipe-edit__instructions {
  margin-top: 10px;
}

.recipe-edit__ingredient-grid {
  display: grid;
  grid-template-columns: repeat(3, auto);
  grid-gap: 10px;
  gap: 10px;
  margin: 20px 0;
}

.recipe-edit__add_ingredient {
  display: flex;
  align-items: center;
}

.recipe-edit__icon-add-ingredient {
  font-size: 15px;
  margin-left: 10px;
}

.recipe-edit__icon-add-ingredient:hover {
  color: pink;
  cursor: pointer;
}

@media (max-width: 768px) {
  .recipe-edit {
    top: 5%;
    left: 5%;
    right: 5%;
    bottom: 5%;
    transform: none;
  }
}

@media (min-width: 768px) {
  .recipe-edit__details-grid {
    grid-template-columns: auto 1fr;
  }
  .recipe-edit__ingredient-grid {
    margin-left: 10px;
  }
}

.search-container {
  display:flex;
  justify-content: flex-end;
  margin-bottom: 40px;
}

.searchbox {
  display:inline-flex;
  align-items: center;
  position: relative;
}

.search {
  font-weight: 100;
  height: 60px;
  font-size: 14px;
  display:inline-block;
  border: none;
  outline: none;
  color: #555;
  position: absolute;
/*  top: 5;*/
  right: 0;
  background: none;
  padding-right: 60px;
  z-index: 3;
  width: 0;
  transition: width 1s cubic-bezier(0.000, 1.000, 0.000, 1.000);
  cursor: pointer;
}

.search:focus {
  width: 200px;
  z-index: 1;
  border-bottom: 1px solid #bbb;
  cursor: text;
}

.search-icon {
  color: #555;
  font-size: 25px;
  cursor: pointer;
  opacity: 0.4;
  position: absolute;
  top: 5;
  right: 0;
  z-index: 2;
  transition: opacity .4s ease;
}

.search-icon:hover {
  opacity: 0.8;
}

@media (min-width: 768px) {
  .search:focus {
    width: 600px;
  }

  .search {
    font-size: 22px;
  }
}

